@import './src/scss/variables';

.buttonLogin {
  padding: 5px 10px;
  border: none;
  background-color: $buttonColor;
  color: $textColor;
  cursor: pointer;
  border-radius: 4px;
  font-weight: 600;
  position: relative;
}
.formContainer{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: $backgroundNav;
  .form{
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    input {
      height: 30px;
      width: 300px;
    }
    button{
      height: 30px;
      background-color: $buttonColor;
      color: $textColor;
      cursor: pointer;
      border-radius: 4px;
      border: 1px solid $textColor;
      font-weight: 600;
    }
  }
}